<template>
  <div>
    <div style="-border:1px solid red;-margin-top:20px;background:#fff;">
      <van-tabs type="card"
                @click="onClick"
                v-model="activeCard">
        <van-tab title="会员列表">
          <van-search v-model="params.searchValue"
                      placeholder="搜索会员名称/手机号" />
          <div id="left_tab"
               style="position:fixed;top:175px;bottom:0px;left:0;right:0;overflow-y:scroll;"
               v-if="info.length != 0">
            <mt-loadmore :top-method="loadTop"
                         :bottom-method="loadBottom"
                         :bottom-all-loaded="allLoaded"
                         ref="loadmore"
                         :autoFill="isAutoFill">
              <div v-for="(item,i) in info"
                   :key="i">
                <div class="members"
                     @click="getDetails(item)"
                     v-if="item.sysUser">
                  <img v-if="item.sysUser.headUrl"
                       :src="item.sysUser.headUrl"
                       alt />
                  <img v-else
                       src="https://wx.qlogo.cn/mmopen/vi_32/9fcFhCxx3sxFwOtNJTsfkn2Us0pDbFv4f88ThciaCRHoSnTjHqXM35USNwv7LLBZliceVvMic8wx9lQkDNNfWEDqA/132"
                       alt />
                  <div>
                    <p class="weight">
                      <span>{{ item.sysUser.nickName }}</span>&ensp;
                      <span style="color:#ff7d41;">{{ item.hotelMemberLevel.memberLevelTemplate.levelName }}</span>
                      &ensp;
                      <span>积分:{{ item.integral?item.integral:'' }}</span>
                    </p>
                    <p class="weight">手机号:{{ item.sysUser.phone }}</p>
                    <p class="c_a6">
                      <span class="w60">消费金额：{{ item.consumeAmount }}元</span>
                      <span style>入住间夜：{{ item.consumeNum?item.consumeNum:'0' }}次</span>
                    </p>
                    <p class="c_a6">
                      <span class="w60">最近入住：{{ item.lastRoomTime?item.lastRoomTime.substr(0,10):'' }}</span>
                    </p>
                  </div>
                </div>
              </div>
            </mt-loadmore>
          </div>
          <div v-else
               id="left_nothing">暂无数据</div>
        </van-tab>
        <van-tab title="批量发劵">
          <!-- 批量发劵 -->
          <div class="scroll_1">
            <div id="send">
              <h4>批量发劵</h4>
              <van-field v-model="price"
                         type="digit"
                         label="发送面额"
                         placeholder="请输入面额" />
              <van-field v-model="digit"
                         type="digit"
                         label="使用门槛"
                         placeholder="满多少金额可使用" />
              <van-field clickable
                         name="calendar"
                         :value="date"
                         label="可用时间"
                         placeholder="点击选择日期"
                         @click="showCalendar = true" />
              <van-calendar v-model="showCalendar"
                            type="range"
                            @confirm="onConfirm" />
            </div>
            <!-- 领取范围 -->
            <div id="range">
              <div class="receive">
                <label>领取范围</label>
                <ul v-if="memLevel.length">
                  <li v-for="(item,index) in memLevel"
                      :key="index">
                    <van-checkbox :value="item.memberLevelTemplate.levelName"
                                  v-model="item.isChecked"
                                  checked-color="#ff7d41"
                                  @click="chooseChange(item.id)">{{item.memberLevelTemplate.levelName}}</van-checkbox>
                  </li>
                </ul>
              </div>
              <div>
              </div>
            </div>
            <div id="range">
              <div class="receive">
                <label>使用范围</label>
                <ul v-if="ordertTypeAll.length">
                  <li v-for="(item,index) in ordertTypeAll"
                      :key="index">
                    <van-checkbox :value="item.dictLabel"
                                  v-model="item.isChecked"
                                  checked-color="#ff7d41"
                                  @click="chooseChange_ordertype(item.dictValue)">{{item.dictLabel}}</van-checkbox>
                  </li>
                </ul>
              </div>
            </div>
            <!-- 确定发布 -->
            <div class="m16">
              <van-button block
                          color="#FF7D41"
                          native-type="submit"
                          @click="release">确定发布</van-button>
            </div>
          </div>
        </van-tab>
        <van-tab title="会员设置">
          <div id="send">
            <!-- <h4>会员设置</h4> -->
            <div class="scroll">
              <div v-for="(item,i) in memSet"
                   :key="i">
                <div class="memSet_box"
                     :style="{'backgroundImage':'url('+item.memberLevelTemplate.domainName+item.memberLevelTemplate.bgUrl+')',backgroundRepeat:'no-repeat',backgroundSize: '100%'}">
                  <div class="left">
                    <p>
                      {{ item.memberLevelTemplate.levelName }}
                      <span v-if="item.thresholdDay == 0"
                            class="c_a6 f7">无门槛领取</span>
                      <span v-else
                            class="c_a6 f7">消费满{{ item.thresholdDay }}间夜达标</span>
                    </p>
                    <p v-if="Number(item.levelDiscount)*10 != 10"
                       class="c_a6 f8">订房{{ Number(item.levelDiscount)*10 }}折</p>
                    <p v-else
                       class="c_a6 f8">无折扣</p>
                    <p class="c_a6 f8">{{ item.depositUnpay == 1?"免押金":' ' }}</p>
                  </div>
                  <div class="right"
                       @click="memberEdit(item)">编辑</div>
                </div>
              </div>
              <div class="m16">
                <van-button v-if="memSet.length"
                            plain
                            color="#000"
                            block
                            type="primary"
                            @click="memberAdd">新增等级</van-button>
                <van-button v-else
                            plain
                            color="#000"
                            block
                            type="primary"
                            @click="memberAddAll">一键新增</van-button>
              </div>
            </div>
          </div>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>
<script>
import { Toast, MessageBox } from 'mint-ui';
import { GetSystemDictDataByType } from '@/api/api_query';
import { GetBusinessHotelMemberPage, GetBusinessHotelMemberLevelByhotel, PostBusinessUserCouponBatchAdd, PostBusinessHotelMemberLevelAddByhotel } from '@/api/api_business_hotel_and_scale';
import { mapMutations } from 'vuex';
export default {
    data() {
        return {
            activeCard: null,
            allLoaded: false, //上拉加载
            isAutoFill: false, // 是否自动触发上拉函数
            offset: 100, //滚动条与底部距离小于 offset 时触发load事件
            params: {
                pageNum: 1,
                pageSize: 10,
                hotelId: '',
                searchValue: ''
            },
            info: [],
            price: '',
            num: '',
            digit: '',
            date: '',
            showCalendar: false,
            showReceive: false,
            message: '',
            memSet: [],
            result: [],
            memLevel: [],
            AllChecked: false,
            isChecked: false,
            selectedData: [],
            selectedData_ordertype: [],
            memName: '',
            memCondition: '',
            memDiscount: '',
            memDeposit: '',
            bbb: false,
            value1: 0,
            doble: [],
            ordertTypeAll: []
        };
    },
    created() {
        this.$store.commit('setHeader', '门店会员');
        this.params.hotelId = this.$route.query.hotelId;
        this.activeCard = this.$store.state.hotelMemberNav;
        this.getMember();
    },

    methods: {
        ...mapMutations(['HOTELMEMBERBERNAV']),
        // 选项卡切换
        onClick(index) {
            this.activeCard = index;
            this.HOTELMEMBERBERNAV(index);
        },
        // 0---分页查询酒店会员
        getMember() {
            GetBusinessHotelMemberPage(this.params).done((res) => {
                res.data = res.data.filter((item) => item.sysUser);
                this.info = res.data;
                if (this.info == null || this.info.length ==
 0) {
                    this.isAutoFill = true;
                    this.allLoaded = true;
                    return;
                }
                if (this.info.length < res.total) {
                    this.allLoaded = false;
                } else if (this.info.length == res.total) {
                    this.allLoaded = true;
                }
            });
        },
        // 0---下拉刷新
        loadTop() {
            this.info = [];
            this.params.pageNum = 1;
            this.getMember();
            this.$refs.loadmore.onTopLoaded();
        },
        // 0---上拉加载请求方法
        loadBottom() {
            this.params.pageNum += 1;
            this.loadMore();
        },
        // 0---上拉加载请求方法
        loadMore() {
            GetBusinessHotelMemberPage(this.params).done((res) => {
                this.info = this.info.concat(res.data);
                if (this.info.length == res.total) {
                    this.allLoaded = true; // 不能进行上拉
                }
            });
        },
        // 0---会员详情
        getDetails(item, index) {
            this.$router.push({
                path: '/memberDetail',
                query: {
                    memberDetail_info: item
                }
            });
            localStorage.setItem('memberDetail_info', JSON.stringify(item));
        },
        // 1---批量-时间
        onConfirm(date) {
            const [start, end] = date;
            this.showCalendar = false;
            this.date = `${this.formatDate(start)} 至 ${this.formatDate(end)}`;
        },
        // 1---批量-时间
        formatDate(date) {
            let doble = `${date.getFullYear()}-${date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1}-${date.getDate() < 10 ? '0' + date.getDate() : date.getDate()}`;
            this.doble.push(doble);
            this.startTime = this.doble[0];
            this.endTime = this.doble[1];
            return `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`;
        },
        // 1---批量-领取范围-查询会员等级模板
        getBatchCoupons() {
            GetBusinessHotelMemberLevelByhotel(this.params.hotelId).done((res) => {
                this.memLevel = res.data;
            });
            GetSystemDictDataByType({ dictType: 'business_order_type' }).done((res) => {
                this.ordertTypeAll = res.data;
            });
        },
        // 单选的change事件-领取范围
        chooseChange(id) {
            if (this.selectedData.indexOf(id) > -1) {
                this.removeByValue(this.selectedData, id);
            } else {
                this.selectedData.push(id);
            }
            this.hotelMemberLevelId = this.selectedData.toString();
        },
        // 过滤
        removeByValue(arr, val) {
            for (var i = 0; i < arr.length; i++) {
                if (arr[i] == val) {
                    arr.splice(i, 1);
                    break;
                }
            }
        },
        // 单选的change事件-使用范围
        chooseChange_ordertype(dictValue) {
            if (this.selectedData_ordertype.indexOf(dictValue) > -1) {
                this.removeByValue(this.selectedData_ordertype, dictValue);
            } else {
                this.selectedData_ordertype.push(dictValue);
            }
            if (this.selectedData_ordertype.length < this.ordertTypeAll.total) {
                this.AllChecked = true;
            } else {
                this.AllChecked = false;
            }
            this.orderType = this.selectedData_ordertype.toString();
        },
        // 确定发布
        release() {
            if (!this.price) {
                Toast({
                    message: '请输入面额',
                    duration: 900
                });
                return false;
            }
            if (!this.digit) {
                Toast({
                    message: '请输入满多少金额可使用',
                    duration: 900
                });
                return false;
            }
            if (!this.startTime) {
                Toast({
                    message: '请输入可用日期',
                    duration: 900
                });
                return false;
            }
            if (!this.hotelMemberLevelId) {
                Toast({
                    message: '请选择领取范围',
                    duration: 900
                });
                return false;
            }
            if (!this.orderType) {
                Toast({
                    message: '请选择使用范围',
                    duration: 900
                });
                return false;
            }
            MessageBox.confirm('', {
                message: '是否发劵？',
                title: '提示',
                cancelButtonText: '否',
                confirmButtonText: '是'
            })
                .then((action) => {
                    if (action == 'confirm') {
                        let params = {
                            couponSource: '2',
                            hotelId: this.params.hotelId,
                            couponAmount: this.price,
                            fullAmount: this.digit,
                            startTime: this.startTime,
                            endTime: this.endTime,
                            hotelMemberLevelIds: this.hotelMemberLevelId,
                            orderType: this.orderType
                        };
                        PostBusinessUserCouponBatchAdd(params).done((res) => {
                            Toast({
                                message: res.msg,
                                duration: 900
                            });
                        });
                        this.price = '';
                        this.digit = '';
                        this.date = '';
                        this.getBatchCoupons();
                        this.selectedData_ordertype = [];
                        this.selectedData = [];
                    }
                })
                .catch((e) => {
                    console.log(e);
                });
        },
        // 2---会员设置---修改
        memberEdit(item) {
            this.$router.push({
                path: '/memberSet',
                query: {
                    memberSet_info: item
                }
            });
            localStorage.setItem('memberSet_info', JSON.stringify(item));
        },
        // 2---会员设置---新增
        memberAdd() {
            this.$router.push({
                path: '/memberSet',
                query: {
                    memberSet_info: ''
                }
            });
            localStorage.setItem('memberSet_info', JSON.stringify(''));
        },
        // 一键新增
        memberAddAll() {
            PostBusinessHotelMemberLevelAddByhotel(this.params.hotelId).done((res) => {
                this.getLevel();
            });
        },
        // 2---查询酒店会员等级
        getLevel() {
            GetBusinessHotelMemberLevelByhotel(this.params.hotelId).done((res) => {
                this.memSet = res.data;
            });
        }
    },
    computed: {
        searchValue() {
            return this.params.searchValue;
        }
    },
    watch: {
        activeCard: function(index) {
            if (index == 0) {
                this.params.searchValue = '';
                this.params.pageNum = 1;
                this.getMember();
            } else if (index == 1) {
                this.getBatchCoupons();
            } else {
                this.getLevel();
            }
        },
        searchValue(newName, oldName) {
            this.params.searchValue = newName;
            this.getMember();
        }
    }
};
</script>
<style scoped>
.c_a6 {
    color: #6a6a6a;
}
.w60 {
    display: inline-block;
    width: 60%;
}
.weight {
    font-weight: bold;
}
.f8 {
    font-size: 0.8rem;
    height: 30px;
}
.f7 {
    font-size: 0.7rem;
    font-weight: normal;
}
.m16 {
    margin: 16px;
}
/* 选项卡 */
/deep/ .van-tabs__nav--card {
    border: 1px solid #fff;
}
/deep/ .van-tabs--card > .van-tabs__wrap {
    padding: 20px 0;
}
/deep/ .van-tabs__nav--card .van-tab.van-tab--active {
    background: #ff7d41;
    color: #fff;
    border-radius: 20px;
}
/deep/ .van-tabs__nav--card .van-tab {
    border-right: #ff7d41;
    color: #6a6a6a;
}
/deep/ .van-search {
    border-bottom: 1px solid #f0f0f0;
}
/deep/ .van-search__content {
    border-radius: 20px;
}
/* 会员列表 */
#left_nothing {
    font-size: 0.8rem;
    margin: 10px 0;
}
.members {
    display: flex;
    padding: 6px 14px;
    background: #fff;
    border-bottom: 1px solid #f0f0f0;
}
.members > img {
    width: 35px;
    height: 35px;
    margin-right: 10px;
    border-radius: 20px;
}
.members > div {
    text-align: left;
    font-size: 0.7rem;
    width: 100%;
    line-height: 26px;
    color: #000;
}
/* 批量发劵 */
.scroll_1 {
    position: fixed;
    top: 120px;
    bottom: 0;
    left: 0;
    right: 0;
    overflow-y: scroll;
}
#send {
    background: #fff;
}
#send > h4 {
    margin: 0;
    text-align: left;
    margin-bottom: 10px;
    padding: 0 32px;
}
#send > .abledTine {
    font-size: 14px;
    padding: 10px 14px;
    text-align: left;
}
/deep/ .van-field__control {
    padding-left: 4%;
}
#range {
    background: #fff;
    padding: 10px 16px;
    width: 100%;
}
#range > .receive {
    font-size: 14px;
    display: flex;
}
#range > .receive > label {
    width: 90px;
    text-align: left;
}
#range > .receive > ul {
    padding-left: 2%;
    display: flex;
    flex-wrap: wrap;
    width: 60%;
}
#range > .receive > .footerflex {
    margin-left: 20px;
}
#range > .receive > ul > li {
    margin-bottom: 10px;
    width: 50%;
}
.van-checkbox-group--horizontal {
    font-size: 0.6rem;
    padding-left: 2%;
}
.van-checkbox--horizontal {
    width: calc(100% / 3);
    margin-bottom: calc(5px * 4 / 2);
}
.van-cell__title {
    display: inline-block;
    width: 20%;
}
.van-cell__value {
    color: #000;
    display: inline-block;
    min-width: 62%;
    text-align: left;
}
#button {
    background: #ff7d41;
}
/* 会员设置 */
.scroll {
    position: fixed;
    top: 144px;
    bottom: 0;
    left: 0;
    right: 0;
    overflow-y: scroll;
}
.memSet_box {
    font-size: 0.9rem;
    margin: 10px 16px;
    padding: 10px;
    border: 1px solid #707070;
    color: #000;
    line-height: 30px;
    background: #fff;
    border-radius: 10px;
    font-weight: bold;
}
.left {
    text-align: left;
    font-size: 0.9rem;
}
.right {
    text-align: right;
    font-size: 0.8rem;
}
.f12 {
    font-size: 0.6rem;
}
.memberBox {
    width: 90%;
    padding-bottom: 10px;
}
#memset {
    margin-top: 20px;
}
#memset > p {
    text-align: left;
    padding: 10px 22px;
    color: #000;
}
#memset > .mem_name {
    padding: 0 8px;
    background: #fff;
}
#memset > .mem_name > .model {
    font-size: 14px;
    width: 100%;
    padding: 5px 16px;
    box-sizing: border-box;
    display: flex;
    border-bottom: 1px solid #ebedf0;
}
#memset > .mem_name > .model > label {
    width: 90px;
    text-align: left;
    display: inline-block;
}
#memset > .mem_name > .model > .span {
    display: inline-block;
    width: 100px;
    padding-left: 2%;
}
.botton_f60 {
    background: #ff7d41;
    color: #fff;
    font-size: 0.8rem;
    width: 90%;
    margin: 30px auto 20px;
}
.botton_f {
    background: #efeff4;
    color: #a8a8aa;
    font-size: 0.8rem;
    width: 90%;
    border: 1px solid #a7a7a9;
}
/deep/ .van-field__label {
    text-align: left;
}
</style>



